import { TimelineMax } from 'gsap';
import conf from './../const';

export default ( wrapper, position, callback = null ) => {
  let tl = new TimelineMax();
  let { titleAnime, titleAnimeSpeed } = conf;

  Object.keys( titleAnime[position] ).forEach(
    (key, index) => {
      if(!wrapper.querySelector(`.${key}`)) return;
      tl.to( wrapper.querySelector(`.${key}`), titleAnimeSpeed, { 
        [titleAnime[position][key].typeScale] : '1',
        transformOrigin : titleAnime[position][key].origin,
      });
    }
  );

  if(callback) callback.call(null, tl, wrapper, titleAnimeSpeed);
};