import animationLine from './../modules/animationLine';
import sm from 'scrollMonitor';
import conf from './../const';
import showText from './../modules/showText';

export default () => {
  
  let animateElem = document.querySelector('.netzwerk-main-text-constructor')

  sm.create(animateElem, { top: animateElem.clientHeight / 2 })
    .one('enterViewport', () => {
      animationLine(
        animateElem,
        'RIGHT'
      );
    });

  document.querySelectorAll('.netzwerk-main-text__p, .netzwerk-main-list-item').forEach(text => showText(text, sm));

}