import animationLine from './../modules/animationLine';
import sm from 'scrollMonitor';
import conf from './../const';
import showText from './../modules/showText';


export default () => {

  document.querySelectorAll('.neuigkeiten-main__title').forEach(title => {
    sm.create(title, { top: title.clientHeight / 2 })
      .one('enterViewport', () => {
        animationLine(
          title, 
          'RIGHT',
          (tl, wrapper, titleAnimeSpeed) => {
            tl.to(title.querySelector('.neuigkeiten-main__title-t'), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 2}` )
          } 
        )
      });
  });

  document.querySelectorAll('.neuigkeiten-main-row-item').forEach(text => showText(text, sm));

};