import $ from 'jquery';
import 'fullpage.js';
import dynamic_title from './../modules/dynamic__title';
import animationLine from './../modules/animationLine';
import sm from 'scrollMonitor';
import showText from './../modules/showText';
import { TimelineMax } from 'gsap';
import conf from './../const';

export default () => {

  [
    {
      target: $('.services-block-rigth-container .index-title'),
      pos: 'Left'
    },
    {
      target: $('.about-block-text .index-title'),
      pos: 'Rigth'
    },
    {
      target: $('.about-block-img'),
      pos: 'Left'
    },
  ].forEach(item => new dynamic_title(item));

  let maxHeight = Object.values($('.fp')).slice(0, -2).reduce((prev, next) => {
    return $(prev).height() > $(next).height() ? prev : next;
  }).clientHeight;

  let bgSectionClass = [
    'ligth',
    'dark',
    'ligth',
    'dark',
    'ligth'
  ];

  let menu;

  let animation = [
    () => {
      // do
    },
    () => {
      animationLine(
        document.querySelector('.about-block-text .index-title'), 
        'RIGHT', 
        (tl, wrapper, titleAnimeSpeed) => {
            tl.to(wrapper.querySelector('.index-title__text'), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 3}`)
        }
      );
      animationLine(
        document.querySelector('.about-block .mouse-wrapper'), 
        'RIGHT'
      );
      animationLine(
        document.querySelector('.about-block-img'), 
        'LEFT'
      );
      document.querySelectorAll('.about-block-text-container__text').forEach(item => showText(item, sm));
    },
    () => {
      animationLine(
        document.querySelector('.services-block .mouse-wrapper'), 
        'RIGHT'
      );
      animationLine(
        document.querySelector('.services-block .index-title'), 
        'LEFT', 
        (tl, wrapper, titleAnimeSpeed) => {
            tl.to(wrapper.querySelector('.index-title__text'), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 3}`)
        }
      );
      document.querySelectorAll('.services-block-left-container__text, .services-block-rigth-container-list li').forEach(item => showText(item, sm));
    },
    () => {
      animationLine(
        document.querySelector('.news-block .mouse-wrapper'), 
        'RIGHT'
      );

      let wrapper = document.querySelector('.news-block-title');
      let tl = new TimelineMax();
  
      tl
        .to(wrapper.querySelector('.left'), conf.titleAnimeSpeed, { scaleY: 1, transformOrigin: '0 100%' })
        .to(wrapper.querySelector('.top'), conf.titleAnimeSpeed, { scaleX: 1, transformOrigin: '0 100%' })
        .to(wrapper.querySelector('.right'), conf.titleAnimeSpeed, { scaleY: 1, transformOrigin: '100% 0' })
    },
    () => {
      animationLine(
        document.querySelector('.contact-index .index-title'), 
        'RIGHT', 
        (tl, wrapper, titleAnimeSpeed) => {
            tl.to(wrapper.querySelector('.index-title__text'), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 3}`)
        }
      );
      document.querySelectorAll('.contact-row-form, .contact-row-list').forEach(item => showText(item, sm));
    }
  ];

  if(maxHeight <= window.innerHeight) {
    $('#fullpage').fullpage({
      sectionSelector: '.fp',
      navigation: true,
      onLeave(index, nextIndex) {
        menu.attr('class', '').addClass(bgSectionClass[nextIndex - 1]);
        if(animation[nextIndex - 1]) {
          setTimeout( () => animation[nextIndex - 1](), 700 );
          setTimeout( () => animation[nextIndex - 1] = null, 701 );
        }
      },
      afterRender() {
        menu = $('#fp-nav');
        menu.addClass('ligth')
      }
    });
  }else {

    document.querySelectorAll('.index-title').forEach(title => {
      sm.create(title, { top: -title.clientHeight }).one('enterViewport',
        () => {
          animationLine(
            title, 
            title.className.includes('right') ? 'RIGHT' : 'LEFT', 
            (tl, wrapper, titleAnimeSpeed) => {
                tl.to(wrapper.querySelector('.index-title__text'), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 3}`)
            }
          )
        }
      );
    });
  
    document.querySelectorAll('.mouse-wrapper').forEach(mouse => {
      sm.create(mouse, { top: -mouse.clientHeight }).one('enterViewport',
        () => {
          animationLine(
            mouse, 
            'RIGHT'
          )
        }
      );
    });
  
  
    sm.create(document.querySelector('.about-block-img'), {top: -document.querySelector('.about-block-img').clientHeight / 2})
      .one('enterViewport', () => {
        animationLine(
          document.querySelector('.about-block-img'), 
          'LEFT'
        )
      });
  
    document.querySelectorAll('.about-block-text-container__text, .services-block-left-container__text, .services-block-rigth-container-list li, .contact-row-form, .contact-row-list')
            .forEach(item => showText(item, sm));
  
    sm.create(
      document.querySelector('.news-block-title'),
      {top: document.querySelector('.news-block-title').clientHeight / 2}
    ).one('enterViewport', () => {
      let wrapper = document.querySelector('.news-block-title');
      let tl = new TimelineMax();
  
      tl
        .to(wrapper.querySelector('.left'), conf.titleAnimeSpeed, { scaleY: 1, transformOrigin: '0 100%' })
        .to(wrapper.querySelector('.top'), conf.titleAnimeSpeed, { scaleX: 1, transformOrigin: '0 100%' })
        .to(wrapper.querySelector('.right'), conf.titleAnimeSpeed, { scaleY: 1, transformOrigin: '100% 0' })
    })

  }

};