import $ from 'jquery';
import dynamic_title from './../modules/dynamic__title';
import animationLine from './../modules/animationLine';
import sm from 'scrollMonitor';
import conf from './../const';
import showText from './../modules/showText';

export default () => {

  $('.main-rechtsgebiete-item-text').each((index, item) => {
    new dynamic_title({
      target: $(item),
      pos: ((index % 2) > 0) ? 'Left' : 'Rigth'   
    });

    
    sm.create(item, {top: -item.clientHeight})
      .one('enterViewport', () => {
        animationLine(
          item, 
          ((index % 2) > 0) ? 'LEFT' : 'RIGHT',
          (tl, wrapper, titleAnimeSpeed) => {
            tl.to(wrapper.querySelector('.main-rechtsgebiete-item-text__s '), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 3}` )
          } 
        );
      });

  });

  document.querySelectorAll('.main-rechtsgebiete-item-list__i').forEach(item => showText(item, sm));

};