import $ from 'jquery';
import IndexControll from './controlls/index';
import RechtsgebieteControll from './controlls/rechtsgebiete';
import UnternehmensdienstleistungControll from './controlls/unternehmensdienstleistung';
import NetzwerkControll from './controlls/netzwerk';
import NeuigkeitenControll from './controlls/neuigkeiten';
import KontaktControll from './controlls/kontakt';
import NewsControll from './controlls/news';

let controlls = {
  index: IndexControll,
  rechtsgebiete: RechtsgebieteControll,
  unternehmensdienstleistung: UnternehmensdienstleistungControll,
  netzwerk: NetzwerkControll,
  neuigkeiten: NeuigkeitenControll,
  kontakt: KontaktControll,
  news: NewsControll
}

$(window).on('load', () => {
  controlls[$('html').data('controlls')]();

  let mobileBtn = $('.header__btn');

  mobileBtn.on('click', () => {
    mobileBtn.toggleClass('open');
    $('.header-mobile-nav').stop().slideToggle(300)
  });

});