import { TimelineMax } from 'gsap';
import conf from './../const';

export default (text, sm, del = 1) => {

  const tl = new TimelineMax();

  tl.set(text, { opacity: 0, y: -30 })

  sm.create(text, {top: -text.clientHeight / del}).one('enterViewport', () => {

    tl.to(text, conf.titleAnimeSpeed * 1.5, {opacity: 1, y: 0})

  });
};