import animationLine from './../modules/animationLine';
import sm from 'scrollMonitor';
import conf from './../const';
import showText from './../modules/showText';

export default () => {

  let title = document.querySelector('.news-wrapper__title');

  sm.create(title, { top: title.clientHeight / 2 })
    .one('enterViewport', () => {
      animationLine(
        title,
        'LEFT',
        // (tl, wrapper, titleAnimeSpeed) => {
        //   tl.to(title.querySelector('.text'), conf.titleAnimeSpeed * 2, { opacity: 1, y:0 }, `-=${conf.titleAnimeSpeed * 3}` )
        // } 
      );
    });

  document.querySelectorAll('.news-one__text, .news-one-item').forEach(text => showText(text, sm));

};