import $ from 'jquery';

export default class dynamic_title {
  constructor({ target, pos }) {
    this.target = target;
    this.init(pos);
  }

  calcWidthLeft() {
    return window.innerWidth - this.target.offset().left;
  }
  calcWidthRigth() {
    return this.target.offset().left;
  }

  installWidth(pos) {
    this.target.attr('style', '')
    this.target.css('width', Math.ceil(this[`calcWidth${pos}`]()) + 'px' );
  }
  installMargin(pos) {
    this.target.attr('style', '')
    this.target.css( 'margin-left', -Math.ceil(this[`calcWidth${pos}`]()) + 'px' );
  }

  init(pos) {
    switch(pos) {
      case 'Left': this.installWidth(pos); 
        $(window).on('resize', this.installWidth.bind(this, pos))      
        break;
      case 'Rigth': this.installMargin(pos); 
        $(window).on('resize', this.installMargin.bind(this, pos))
        break;
    }
    
  }
};